.common_main_container {
  width: 100%;
  height: 100%;
  background-image: url(/static/img/bg.89a510d.jpg);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: auto;
}
.common_main_container .list li {
    width: 100%;
}
.common_header_wrapper {
  height: 2rem;
  z-index: 1;
  line-height: 2rem;
  color: #fff;
  position: relative;
}
.common_header_wrapper .left_wrapper {
    display: inline-block;
    min-width: 12rem;
    height: 100%;
    vertical-align: middle;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
}
.common_header_wrapper .left_wrapper .previous {
      display: inline-block;
      width: 2rem;
      font-size: 1rem;
      color: #fff;
      text-align: center;
}
.common_header_wrapper .middle_wrapper {
    width: 100%;
    text-align: center;
    font-size: 0.8rem;
}
.common_header_wrapper .right_wrapper {
    vertical-align: middle;
}
.common_header_wrapper .right_wrapper .splitline {
      display: inline-block;
      width: 1px;
      height: 25px;
      background-color: #000;
      vertical-align: middle;
}
.common_header_wrapper .right_wrapper .setting {
      display: inline-block;
      padding: 0 5px;
      color: #666;
      height: 30px;
      vertical-align: middle;
}
.common_header_wrapper .right_wrapper .setting > span {
        font-size: 20px;
        line-height: 20px;
}
.common_header_wrapper .right_wrapper .setting > span span {
          font-size: 30px;
}
.common_header_wrapper .right_wrapper:after {
      content: '';
      display: inline-block;
      height: 50px;
      vertical-align: middle;
}
.common_header_wrapper .common-useravatar-item {
    float: right;
    margin: 10px;
    position: relative;
}
.common_header_wrapper .common-useravatar-item .avatar {
      display: inline-block;
      vertical-align: middle;
      overflow: hidden;
}
.common_header_wrapper .common-useravatar-item .avatar img {
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
}
.common_input_item {
  width: 100%;
  height: 1.3rem;
  font-size: 0.5rem;
  padding: 0 0.3rem;
  box-sizing: border-box;
  vertical-align: top;
}
.common_button_item {
  display: inline-block;
  border: 0;
  line-height: 1.2rem;
  width: 100%;
  height: 1.3rem;
  font-size: 0.5rem;
  padding: 0 0.3rem;
  box-sizing: border-box;
  color: #fff;
  background: #c2c2c2;
  border-radius: 0.6rem;
}
.common_button_item[type='text'] {
    width: auto;
    font-size: 0.6rem;
    background: none;
    color: #1890ce;
}
.common_button_item.active {
    background: #f36619;
}
.common_form_item {
  position: relative;
}
.common_form_item .common_button_item {
    position: absolute;
    right: 0;
    top: 0;
}
.common_blocktitle_item {
  width: 100%;
  height: 1.5rem;
  text-align: center;
}
.common_blocktitle_item span {
    display: inline-block;
    width: 5rem;
    height: 100%;
    vertical-align: middle;
    font-size: 1rem;
    position: relative;
}
.common_blocktitle_item span i {
      display: inline-block;
      width: 100%;
      height: 1px;
      line-height: 1.5rem;
      background: #fff;
      vertical-align: middle;
}
.common_blocktitle_item p {
    display: inline-block;
    text-align: center;
    color: #fff;
    font-size: 0.6rem;
    vertical-align: middle;
}
.common_loading_wrapper {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
.share_main_wrapper {
  margin: 0 auto;
  width: 15rem;
}
.share_main_wrapper .carousel {
    margin: 0 auto;
    width: 100%;
    height: 8.9rem;
    box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
    background-color: #fff;
    border-radius: 0.25rem;
    overflow: hidden;
}
.share_main_wrapper .carousel img {
      display: block;
      width: 100%;
}
.share_main_wrapper .form {
    margin: 0.5rem auto;
    width: 100%;
    height: 9rem;
    border-radius: 0.25rem;
    background-color: rgba(0, 0, 0, 0.5);
    overflow: hidden;
}
.share_main_wrapper .form .title {
      margin: 0.3rem 0;
      text-align: center;
      color: #fff;
      line-height: 1rem;
}
.share_main_wrapper .form .title h1 {
        font-size: 0.8rem;
        letter-spacing: 0.7rem;
}
.share_main_wrapper .form .title h2 {
        font-size: 0.7rem;
        letter-spacing: 0.05rem;
}
.share_main_wrapper .form .main li {
      margin: 0.5rem auto;
      width: 11rem;
      text-align: center;
}
.share_main_wrapper .ranklist li {
    margin: 1rem 0 0 0;
    width: 100%;
    font-size: 0;
}
.share_main_wrapper .ranklist li .avatar {
      display: inline-block;
      width: 2rem;
      height: 2rem;
      border: 1px dashed #fff;
      border-radius: 50%;
      position: relative;
      vertical-align: top;
      text-align: center;
}
.share_main_wrapper .ranklist li .avatar img {
        display: inline-block;
        width: 98.5%;
        z-index: 1;
        position: relative;
        top: 1%;
        border-radius: 50%;
}
.share_main_wrapper .ranklist li .avatar .query {
        display: inline-block;
        width: 100%;
        color: #fff;
        text-align: center;
        line-height: 2rem;
        position: absolute;
        top: 0;
        left: 0;
        font-size: 1rem;
}
.share_main_wrapper .ranklist li .detail {
      display: inline-block;
      padding: 0 0 0 0.5rem;
      box-sizing: border-box;
      width: 10.8rem;
      color: #fff;
      vertical-align: top;
      font-size: 0.7rem;
}
.share_main_wrapper .ranklist li .detail .name {
        width: 10rem;
}
.share_main_wrapper .ranklist li .detail .name label {
          display: inline-block;
          max-width: 5rem;
          color: #ffde02;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          vertical-align: middle;
}
.share_main_wrapper .ranklist li .detail .name span {
          display: inline-block;
          margin: 0 0 0 0.3rem;
          vertical-align: middle;
}
.share_main_wrapper .ranklist li .detail .comment {
        margin: 0.3rem 0 0 0;
}
.share_main_wrapper .ranklist li .prizename {
      display: inline-block;
      width: 2rem;
      color: #ffde02;
      vertical-align: top;
      font-size: 0.7rem;
}
.share_main_wrapper .ranklist .hint {
    margin: 1rem 0;
    text-align: center;
    font-size: 0.7rem;
    color: #ffde02;
}
.share_main_wrapper .prize_wrapper {
    margin: 1rem 0 0 0;
    overflow: hidden;
    background: url(data:image/png;base64,) no-repeat;
    background-size: cover;
}
.share_main_wrapper .prize_wrapper h1 {
      margin: 0.3rem 0;
      text-align: center;
      color: #FE370E;
      font-size: 0.8rem;
}
.share_main_wrapper .prize_wrapper .main {
      height: 5rem;
      font-size: 0;
}
.share_main_wrapper .prize_wrapper .main .withpicture {
        text-align: center;
}
.share_main_wrapper .prize_wrapper .main .withpicture .prizeimage {
          display: inline-block;
          width: 40%;
          height: 100%;
          vertical-align: middle;
          text-align: center;
}
.share_main_wrapper .prize_wrapper .main .withpicture .prizeimage img {
            display: inline-block;
            width: 4rem;
}
.share_main_wrapper .prize_wrapper .main .withpicture .detail {
          display: inline-block;
          width: 50%;
          height: 100%;
          font-size: 0.3rem;
          text-align: center;
          vertical-align: middle;
}
.share_main_wrapper .prize_wrapper .main .withpicture .detail label {
            display: block;
            margin: 0.5rem 0;
            font-size: 0.7rem;
            font-weight: bold;
}
.share_main_wrapper .prize_wrapper .main .withpicture .detail span {
            display: block;
            margin: 0.5rem 0;
            font-size: 0.5rem;
}
.share_main_wrapper .prize_wrapper .main .withpicture .detail button {
            background: #f36619;
            padding: 0.3rem 0.5rem;
            border-radius: 2rem;
            color: #fff;
            font-size: 0.5rem;
}
.share_main_wrapper .prize_wrapper .main .withoutpicture .prizeimage {
        display: inline-block;
        width: 50%;
        height: 100%;
        vertical-align: middle;
}
.share_main_wrapper .prize_wrapper .main .withoutpicture .detail {
        display: inline-block;
        width: 100%;
        height: 100%;
        font-size: 0.3rem;
        text-align: center;
        vertical-align: middle;
}
.share_main_wrapper .prize_wrapper .main .withoutpicture .detail label {
          color: #f36619;
          display: block;
          margin: 0.5rem 0;
          font-size: 0.7rem;
          font-weight: bold;
}
.share_main_wrapper .prize_wrapper .main .withoutpicture .detail span {
          display: block;
          margin: 0.5rem 0;
          font-size: 0.7rem;
}
.share_main_wrapper .prize_wrapper .main .withoutpicture .detail button {
          background: #f36619;
          padding: 0.3rem 0.5rem;
          border-radius: 2rem;
          color: #fff;
          font-size: 0.5rem;
}
.share_main_wrapper .rules li {
    margin: 0.5rem 0;
    color: #eee;
    font-size: 0.7rem;
}
.share_main_wrapper .notfound404 {
    background: #fff;
    text-align: center;
}
body {
  background-color: #fbf9fe;
}
